<script>

import { mapActions, mapState, mapGetters } from 'vuex';
import PointTable from '../components/PointTable';


export default {

  name: 'Points',
  components: {
    PointTable,
  },
  data() {
    return {
      showConfirmationModal: false,
      newPointList: null,
      page: {
        currentPage: 1,
        rows: null,
        perPage: 20,
      },
      sort: {
        field: 'mac',
        direction: 'asc',
      },
    };
  },

  computed: {

    ...mapState(
      'pointList', {
        loadedPointList: 'status',
        pointList: 'pointList',
      },
    ),

    ...mapState(
      'points', {
        pointsEvent: 'pointsEvent',
        loadedPointEvent: 'status',
      },
    ),

    ...mapState(
      'pointClient', {
        pointClient: 'pointClient',
        loadedPointClient: 'status',
      },
    ),

    ...mapGetters({
      pointListForOption: 'pointList/pointList',
    }),

    dataLoaded() {
      return this.loadedPointEvent === 'LOADED' && this.loadedPointList === 'LOADED' && this.page.rows !== null;
    },

  },

  mounted() {
    this.getPointList();
    this.updatePoint();
  },

  methods: {

    ...mapActions({
      getPointsEvent: 'points/getPointsEvent',
      setPointList: 'points/updatePointList',
      getPointList: 'pointList/getPointList',
      getClientEventByPoint: 'pointClient/getClientEventByPoint',
    }),

    updatePointList({ pointId, pointListId }) {
      this.setPointList({
        pointId,
        pointList: this.pointList.filter(l => (l.id === pointListId))[0],
      });
    },

    async updatePoint() {
      const offset = (this.page.currentPage - 1) * this.page.perPage;
      await this.getPointsEvent({
        offset,
        limit: this.page.perPage,
        sortBy: `${this.sort.field}.${this.sort.direction}`,
      });
      this.page.rows = this.pointsEvent[0].count;
    },

    async getPointClientByPoint(pointId) {
      await this.getClientEventByPoint(pointId);
    },
  },


};
</script>

<template>
  <div>
    <div>
      <div>Фильтры</div>
      <div>Карта</div>
      <template v-if="dataLoaded">
        <point-table
          :points-event="pointsEvent"
          :point-list="pointListForOption"
          :point-client="pointClient"
          :loaded-point-client="loadedPointClient"
          :page.sync="page"
          :sort.sync="sort"
          @updatePointList="updatePointList"
          @updatePoint="updatePoint"
          @getPointClientByPoint="getPointClientByPoint"
        />
      </template>
    </div>
  </div>
</template>
