<script>
export default {
  name: 'ExpandPointTable',
  components: {},
  props: {
    pointClient: {
      required: true,
    },
  },
  data() {
    return {
      fields: [
        { key: 'timeFrame', label: '', class: 'timeFrame' },
        { key: 'mac', label: 'MAC-адрес', sortable: true },
        { key: 'vendor', label: 'Производитель', sortable: true },
        { key: 'lastDateTime', label: 'Время активности', sortable: true },
        { key: 'packages', label: 'Пакеты', sortable: true },
        { key: 'frequency', label: 'Канал', sortable: true },
        { key: 'level', label: 'Уровень', sortable: true },
      ],
    };
  },
  computed: {
    pointClientSetClass() {
      return this.pointClient.map(
        cl => ({ ...cl, _cellVariants: { timeFrame: cl.timeFrame } }),
      );
    },
  },
};
</script>

<template>
  <b-table
    hover
    small
    thead-class="thead-light"
    :items="pointClientSetClass"
    :fields="fields"
  >
    <template
      slot="timeFrame"
      slot-scope="row"
      :class="row"
    />
  </b-table>
</template>

