<script>

import ConfirmationOfActionsModal from '../components/СonfirmationOfActionsModal';
import ExpandPointTable from '../components/ExpandPointTable';

export default {
  name: 'Table',
  components: {
    ConfirmationOfActionsModal,
    ExpandPointTable,
  },

  props: {
    pointsEvent: {
      required: true,
    },
    pointList: {
      required: true,
    },
    pointClient: {
      required: false,
    },
    loadedPointClient: {
      required: false,
    },
    page: {
      required: true,
    },
    sort: {
      required: true,
    },
  },

  data() {
    return {
      showConfirmationModal: false,
      newPointList: null,
      perPages: [5, 10, 20, 50, 100],
      perPage: 20,
      sortBy: 'mac',
      showExpandAdditionalInfo: [],
      fields: [
        { key: 'timeFrame', label: '', class: 'timeFrame' },
        { key: 'mac', label: 'MAC-адрес', sortable: true },
        { key: 'name', label: 'Имя', sortable: true },
        { key: 'vendor', label: 'Производитель', sortable: true },
        { key: 'clientCount', label: 'Клиенты', sortable: true },
        { key: 'lastDateTime', label: 'Время активности', sortable: true },
        { key: 'packages', label: 'Пакеты', sortable: true },
        { key: 'frequency', label: 'Канал', sortable: true },
        { key: 'level', label: 'Уровень', sortable: true },
        { key: 'pointList', label: 'Список', sortable: true },
      ],
    };
  },
  computed: {
    pointsEventSetClass() {
      return this.pointsEvent.map(
        p => ({
          ...p,
          _cellVariants: { timeFrame: p.timeFrame },
          _showDetails: this.showExpandAdditionalInfo.includes(p.id),
          _dataLoaded: this.dataLoaded,
        }),
      );
    },
    dataLoaded() {
      return this.loadedPointClient === 'LOADED' && this.pointClient.length > 0;
    },
  },


  methods: {

    changeShowConfirmationModal() {
      this.showConfirmationModal = !this.showConfirmationModal;
    },

    updatePointList(event, { id }) {
      this.changeShowConfirmationModal();
      this.newPointList = { pointId: id, pointListId: event };
    },

    yes() {
      this.$emit('updatePointList', this.newPointList);
    },

    no() {
      this.$emit('updatePoint');
    },

    changeCurrentPage(currentPage) {
      if (this.page.currentPage !== currentPage) {
        this.page.currentPage = currentPage;
        this.$emit('update:page', this.page);
        this.$emit('updatePoint');
        this.showExpandAdditionalInfo = [];
      }
    },

    changePerPage() {
      this.page.currentPage = 1;
      this.$emit('update:page', this.page);
      this.$emit('updatePoint');
      this.showExpandAdditionalInfo = [];
    },

    sortProvider({ sortBy, sortDesc }) {
      this.sort.field = sortBy;
      this.sort.direction = sortDesc ? 'desc' : 'asc';
      this.$emit('update:sort', this.sort);

      this.page.currentPage = 1;
      this.$emit('update:page', this.page);

      this.$emit('updatePoint');
      this.showExpandAdditionalInfo = [];
    },

    expandAdditionalInfo(row) {
      // eslint-disable-next-line no-underscore-dangle
      if (!row._showDetails) {
        this.showExpandAdditionalInfo.push(row.id);
        this.$emit('getPointClientByPoint', row.id);
      } else {
        this.showExpandAdditionalInfo = this.showExpandAdditionalInfo.filter(val => val !== row.id);
      }
    },
    // eslint-disable-next-line
    sortCompare(a, b, key) {
      return 0;
    },
  },
};
</script>

<template>
  <div>
    <b-table
      hover
      striped
      :sort-by.sync="sort.field"
      thead-class="thead-light"
      :items="pointsEventSetClass"
      :fields="fields"
      :sort-compare="sortCompare"
      class="color"
      @sort-changed="sortProvider"
      @row-clicked="expandAdditionalInfo"
    >
      <template
        slot="timeFrame"
        slot-scope="row"
        :class="row"
      />
      <template
        slot="pointList"
        slot-scope="row"
      >
        <b-form-select
          v-model="row.item.pointList.id"
          style="z-index: 999"
          :options="pointList"
          @change="updatePointList($event, row.item)"
        />
      </template>
      <template
        slot="row-details"
        slot-scope="row"
      >
        <b-card>
          <table class="table table-borderless text-center">
            <tr>
              <td><b>Этаж:</b></td>
              <td>{{ row.item.flat }}</td>
              <td><b>Стандарт:</b></td>
              <td>{{ row.item.standard }}</td>
              <td><b>Тип шифрования:</b></td>
              <td>{{ row.item.coding }}</td>
              <td><b>Время появления:</b></td>
              <td>{{ row.item.firstDateTime }}</td>
              <td><b>Время последней активности:</b></td>
              <td>{{ row.item.lastDateTime }}</td>
            </tr>
          </table>
          <div v-if="row.item.clients">
            <div><b>Подключенные клиенты</b></div>
            <expand-point-table
              :point-client="row.item.clients"
            />
          </div>
        </b-card>
      </template>
    </b-table>
    <b-row>
      <b-col cols="11">
        <b-pagination
          v-model="page.currentPage"
          :total-rows="page.rows"
          :per-page="page.perPage"
          size="md"
          align="center"
          @change="changeCurrentPage"
        />
      </b-col>
      <b-col>
        <b-select
          v-model="page.perPage"
          :options="perPages"
          @change="changePerPage"
        />
      </b-col>
    </b-row>
    <confirmation-of-actions-modal
      :show-modal.sync="showConfirmationModal"
      text-confirmation="Переместить Wi-Fi точку доступа в выбранный список?"
      @yes="yes"
      @no="no"
    />
  </div>
</template>

<style scoped>
  >>> .color  {
    background-color: white;
  }
  >>> .timeFrame {
    width: 6px;
    padding-left: 0px;
    padding-right: 0px;

  }
  >>> .table-MORE_300 {
    background-color: red;
  }
  >>> .table-LESS_300 {
    background-color: yellow;
  }
  >>> .table-LESS_60 {
    background-color: blue;
  }
  >>> .table-LESS_30 {
    background-color: green;
  }

  >>> td {
    vertical-align: middle;
  }

  >>> th {
    cursor: pointer;
    -webkit-touch-callout: none;
