<script>
export default {
  name: 'ConfirmationOfActionsModal',
  components: {},
  props: {
    showModal: {
      type: Boolean,
      required: true,
    },
    textConfirmation: {
      type: String,
      required: true,
    },
  },
  methods: {
    yes() {
      this.$emit('update:showModal', !this.showModal);
      this.$emit('yes');
    },
    no() {
      this.$emit('update:showModal', !this.showModal);
      this.$emit('no');
    },
  },
};
</script>

<template>
  <b-modal
    :visible="showModal"
    :hide-header="true"
    :hide-footer="true"
    centered
  >
    <p class="text-center">
      {{ textConfirmation }}
    </p>
    <div class="text-center">
      <b-button
        variant="primary"
        @click="yes"
      >
        Да
      </b-button>
      <b-button
        variant="danger"
        @click="no"
      >
        Нет
      </b-button>
    </div>
  </b-modal>
</template>

<style scoped>
  >>> p {
    margin: 1rem;
  }
  >>> button {
    margin: 0rem 0.2rem;
    min-width: 4rem;
  }
</style>
